.flex-table {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  max-width: 100%;
  padding-bottom: 10px;
}

.flex-item {
  -webkit-flex-grow: 0;
  flex-grow: 0;
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
  width: 100%;
}

.flex-item span {
  margin-bottom: 0;
  margin-top: 0;
  padding-top: 0;
  display: block;
}

.flex-colspan-2 {
  flex-grow: 1;
  width: 100%;
}

.hidden {
  display: none;
}

.sub-text {
  font-size: 11px;
  margin-left: 30px;
  font-style: italic;
}

#description {
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  border-bottom: 1px solid;
  padding-bottom: 10px;
}

#calculate {
  max-height: 50px;
  cursor: pointer;
  background-color: #81313a;
  color: #fff;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
}

input,
select {
  min-width: 100%;
}

@media (min-width: 350px) {
  .flex-table {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    -webkit-flex-wrap: nowrap;
    flex-wrap: nowrap;
    max-width: 350px;
    margin-left: auto;
    margin-right: auto;
  }

  .flex-row {
    display: flex;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    flex-wrap: nowrap;
    max-width: 100%;
    padding-bottom: 10px;
  }

  .flex-item {
    -webkit-flex-grow: 0;
    flex-grow: 0;
    -webkit-flex-shrink: 0;
    flex-shrink: 0;
    width: 50%;
  }

  .flex-item span {
    margin-bottom: 0;
    margin-top: 0;
    padding-top: 0;
    display: block;
  }

  .flex-colspan-2 {
    flex-grow: 1;
    width: 100%;
  }

  .hidden {
    display: none;
  }

  .sub-text {
    font-size: 11px;
    margin-left: 30px;
    font-style: italic;
  }

  #description {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid;
    padding-bottom: 10px;
  }

  #calculate {
    max-height: 50px;
    cursor: pointer;
    height: 100px;
    background-color: #81313a;
    color: #fff;
    margin-left: auto;
    margin-right: auto;
  }

  input,
  select {
    min-width: 100%;
  }
}
